import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import API from '../api/axios';
import {
  Box,
  Typography,
  Paper,
  Grid,
  Button,
  CircularProgress,
  Alert,
  List,
  ListItem,
  ListItemText,
  ListItemSecondaryAction,
  IconButton,
  Divider,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogContentText,
  DialogActions,
  Chip
} from '@mui/material';
import { Delete, ShoppingCart } from '@mui/icons-material';
import { toast } from 'react-hot-toast';
import { useAuth } from '../contexts/AuthContext';

const Cart = () => {
  const [items, setItems] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState('');
  const [processingOrder, setProcessingOrder] = useState(false);
  const [confirmDialogOpen, setConfirmDialogOpen] = useState(false);
  const [orderSuccess, setOrderSuccess] = useState(false);
  const [orderDetails, setOrderDetails] = useState(null);
  const navigate = useNavigate();
  const { user } = useAuth();

  const fetchCartItems = async () => {
    try {
      setLoading(true);
      setError('');
      const response = await API.get('/cart');
      setItems(response.data.data.items);
    } catch (err) {
      setError(err.response?.data?.message || 'Failed to fetch cart items');
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    fetchCartItems();
  }, []);

  const handleRemoveFromCart = async (itemId) => {
    try {
      await API.delete(`/cart/${itemId}`);
      fetchCartItems();
    } catch (err) {
      setError(err.response?.data?.message || 'Failed to remove item from cart');
    }
  };

  const handlePlaceOrder = async () => {
    try {
      setProcessingOrder(true);
      setError('');

      // Filter out user's own items
      const validItems = items.filter(item => item.seller._id !== user.id);
      
      if (validItems.length === 0) {
        setError('Please remove your own items from cart to place order');
        setProcessingOrder(false);
        return;
      }

      // Create orders for each item
      const orderPromises = validItems.map(item =>
        API.post('/orders', { 
          itemId: item._id,
          quantity: 1
        })
      );

      const results = await Promise.all(orderPromises);
      
      // Clear cart after successful orders
      await API.delete('/cart');
      
      // Show order details with OTPs
      setOrderDetails(results.map(result => ({
        item: result.data.data.order.item,
        transactionId: result.data.data.order.transactionId,
        otp: result.data.data.order.otp
      })));
      
      setOrderSuccess(true);
      setConfirmDialogOpen(false);
      fetchCartItems();
    } catch (err) {
      setError(err.response?.data?.message || 'Failed to place order');
    } finally {
      setProcessingOrder(false);
    }
  };

  const calculateTotal = () => {
    return items.reduce((total, item) => total + item.price, 0);
  };

  const getReservationStatus = (item) => {
    if (item.status === 'reserved' && item.reservedBy === user.id) {
      return 'Reserved for you';
    }
    return item.status === 'available' ? 'Available' : 'Not available';
  };

  if (loading) {
    return (
      <Box display="flex" justifyContent="center" alignItems="center" minHeight="60vh">
        <CircularProgress />
      </Box>
    );
  }

  return (
    <Box>
      <Typography variant="h4" gutterBottom>
        Shopping Cart
      </Typography>

      {error && <Alert severity="error" sx={{ mb: 2 }}>{error}</Alert>}

      {items.length === 0 ? (
        <Paper sx={{ p: 3, textAlign: 'center' }}>
          <ShoppingCart sx={{ fontSize: 60, color: 'text.secondary', mb: 2 }} />
          <Typography variant="h6" color="text.secondary" gutterBottom>
            Your cart is empty
          </Typography>
          <Button
            variant="contained"
            onClick={() => navigate('/search')}
            sx={{ mt: 2 }}
          >
            Continue Shopping
          </Button>
        </Paper>
      ) : (
        <Grid container spacing={3}>
          <Grid item xs={12} md={8}>
            <Paper sx={{ p: 2 }}>
              <List>
                {items.map((item, index) => (
                  <React.Fragment key={item._id}>
                    {index > 0 && <Divider />}
                    <ListItem>
                      <Box sx={{ mr: 2 }}>
                        {item.image ? (
                          <img
                            src={`data:${item.image.contentType};base64,${item.image.data}`}
                            alt={item.name}
                            style={{ width: 80, height: 80, objectFit: 'cover' }}
                          />
                        ) : (
                          <Box
                            sx={{
                              width: 80,
                              height: 80,
                              bgcolor: 'grey.200',
                              display: 'flex',
                              alignItems: 'center',
                              justifyContent: 'center'
                            }}
                          >
                            No Image
                          </Box>
                        )}
                      </Box>
                      <ListItemText
                        primary={item.name}
                        secondary={
                          <>
                            <Typography component="span" variant="body2" color="text.primary">
                              {item.price}
                            </Typography>
                            <br />
                            <Typography component="span" variant="body2">
                              Seller: {item.seller.firstName} {item.seller.lastName}
                            </Typography>
                            <br />
                            <Chip
                              size="small"
                              label={getReservationStatus(item)}
                              color={item.status === 'available' ? 'success' : 'warning'}
                              sx={{ mt: 1 }}
                            />
                          </>
                        }
                      />
                      <ListItemSecondaryAction>
                        <IconButton
                          edge="end"
                          onClick={() => handleRemoveFromCart(item._id)}
                        >
                          <Delete />
                        </IconButton>
                      </ListItemSecondaryAction>
                    </ListItem>
                  </React.Fragment>
                ))}
              </List>
            </Paper>
          </Grid>
          <Grid item xs={12} md={4}>
            <Paper elevation={3} sx={{ p: 2 }}>
              <Typography variant="h6" gutterBottom>
                Order Summary
              </Typography>
              <Divider sx={{ my: 2 }} />
              <Box sx={{ display: 'flex', justifyContent: 'space-between', mb: 2 }}>
                <Typography>Total Items:</Typography>
                <Typography>{items.length}</Typography>
              </Box>
              <Box sx={{ display: 'flex', justifyContent: 'space-between', mb: 2 }}>
                <Typography variant="h6">Total Amount:</Typography>
                <Typography variant="h6">{calculateTotal()}</Typography>
              </Box>
              <Button
                variant="contained"
                color="primary"
                fullWidth
                onClick={() => setConfirmDialogOpen(true)}
                disabled={processingOrder || items.length === 0}
              >
                Place Order
              </Button>
            </Paper>
          </Grid>
        </Grid>
      )}

      <Dialog 
        open={confirmDialogOpen} 
        onClose={() => !processingOrder && setConfirmDialogOpen(false)}
      >
        <DialogTitle>Confirm Order</DialogTitle>
        <DialogContent>
          <DialogContentText>
            Are you sure you want to place orders for {items.length} item(s)?
            Total amount: {calculateTotal()}
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button 
            onClick={() => setConfirmDialogOpen(false)}
            disabled={processingOrder}
          >
            Cancel
          </Button>
          <Button
            onClick={handlePlaceOrder}
            variant="contained"
            disabled={processingOrder}
          >
            {processingOrder ? <CircularProgress size={24} /> : 'Confirm'}
          </Button>
        </DialogActions>
      </Dialog>

      <Dialog
        open={orderSuccess}
        onClose={() => setOrderSuccess(false)}
      >
        <DialogTitle>Order Placed Successfully!</DialogTitle>
        <DialogContent>
          <DialogContentText>
            Your orders have been placed successfully. Please save the following OTPs to share with the sellers when receiving your items:
          </DialogContentText>
          <List>
            {orderDetails?.map((order, index) => (
              <ListItem key={index}>
                <ListItemText
                  primary={`Item: ${order.item.name}`}
                  secondary={
                    <>
                      <Typography variant="body2">
                        Transaction ID: {order.transactionId}
                      </Typography>
                      <Typography variant="body2" color="primary">
                        OTP: {order.otp}
                      </Typography>
                    </>
                  }
                />
              </ListItem>
            ))}
          </List>
        </DialogContent>
        <DialogActions>
          <Button 
            onClick={() => {
              setOrderSuccess(false);
              navigate('/orders');
            }} 
            variant="contained"
          >
            View Orders
          </Button>
        </DialogActions>
      </Dialog>
    </Box>
  );
};

export default Cart; 